.symptoms-container {
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.search-bar {
  display: block;
  margin: 0 auto 20px;
  padding: 10px;
  width: 100%;
  max-width: 400px;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.symptoms-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 10px;
}

.symptom-item {
  display: flex;
  align-items: center;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
  cursor: pointer;
  transition: background-color 0.2s;
}

.symptom-item:hover {
  background-color: #e9e9e9;
}

.symptom-item input {
  margin-right: 10px;
}

.clear-button, .detect-button {
  display: block;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 150px;
}

.clear-button {
  background-color: #f44336;
  color: white;
}

.clear-button:hover {
  background-color: #c62828;
}

.detect-button {
  background-color: #4caf50;
  color: white;
}

.detect-button:hover {
  background-color: #388e3c;
}

.spinner {
  border: 4px solid #f3f3f3;
  border-radius: 50%;
  border-top: 4px solid #3498db;
  width: 16px;
  height: 16px;
  animation: spin 2s linear infinite;
  display: inline-block;
  vertical-align: middle;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.selected-symptoms {
  margin-top: 20px;
}

.selected-symptoms ul {
  list-style-type: none;
  padding: 0;
}

.selected-symptoms li {
  padding: 5px;
  border-bottom: 1px solid #ddd;
}

.selected-symptoms p {
  text-align: center;
  color: #888;
}

.detected-disease {
  margin-top: 20px;
  text-align: center;
  font-size: 18px;
  color: #333;
}

.error-message {
  color: red;
  text-align: center;
  margin-top: 20px;
}
